		},
		methods:{
			pcFixed(){
				let htmlWidth = document.documentElement.clientWidth
				let htmlHeight = document.documentElement.clientHeight
				
				let htmlDom = document.getElementsByTagName('html')[0];

				if(htmlWidth > htmlHeight){
					htmlDom.style.transform = 'scale(0.5)'
					htmlDom.style.transformOrigin = 'top'
				}else{
					htmlDom.style.transform = 'scale(1)'
					htmlDom.style.transformOrigin = 'center'
				}
			}
		},
		mounted(){
			this.pcFixed()
			window.onresize=()=>{
				this.pcFixed()
				console.log(11)
			}
		}
	}
</script>
